:root {
    --bg: #01081c;
}

#root {
    min-height: 100vh;
    position: relative;
    background-color: var(--bg);
}

a {
    text-decoration: none;
}

#content-wrap {
    padding-bottom: 2.5rem;
}

header,footer {
    height: 2.5rem;
    width: 100%;
    line-height: 2.5rem;
    background-color: var(--bg);
}